/* Футер */
.footer-content {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: -100%;
  z-index: 100;
}

/* Картинка фону футера */
.footerbg {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.footer-buttons {
  position: absolute;
  bottom: 25%; /* регулюй це під фон */
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  justify-content: center;
  gap: 10vw;
  z-index: 10;
  -webkit-tap-highlight-color: transparent;
}

.footer-button-image {
  width: 20vw;     /* адаптуй під свої розміри */
  height: auto;
  cursor: pointer;
  transition: transform 0.1s ease;
}

.footer-button-image:active {
  transform: scale(0.95);
}