.background-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 0;
}

.content {
  position: relative;
  z-index: 1;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 0 16px; /* щоб уникнути прилипань */
  box-sizing: border-box;
}

.interactive-area {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 2; /* вище фону */
  pointer-events: none; /* щоб не заважало всьому іншому */
}

.map-button {
  position: absolute;
  width: 30vw;
  height:30vw;
  background: transparent;
  border: none;
  cursor: pointer;
  pointer-events: all; /* Увімкнути тільки для кнопок */
}

.plant-button {
  position: absolute;
  width: 8vw;
  height:8vw;
  background: transparent;
  border: none;
  cursor: pointer;
  pointer-events: all; /* Увімкнути тільки для кнопок */
}



